<template>
	<section class="uk-section uk-section-muted">
		<div class="uk-card uk-card-default uk-card-body uk-width-1-3 uk-align-center">

			<form @submit.stop.prevent="handleSubmit">
				<fieldset class="uk-fieldset">

					<legend class="uk-legend uk-text-center">{{'REGISTER' | translate}}</legend>

					<div class="uk-margin">
						<label class="uk-form-label">{{'USERNAME' | translate}}</label>
						<input class="uk-input" v-model="username" type="text"  placeholder="pedro">
					</div>

					<div class="uk-margin">
						<label class="uk-form-label" for="form-stacked-text">{{'EMAIL' | translate}}</label>
						<input class="uk-input" v-model="email" type="email" placeholder="pedro@gmail.com">
					</div>

					<div class="uk-margin">
						<label class="uk-form-label" for="form-stacked-text">{{'PASSWORD' | translate}}</label>
						<input class="uk-input" v-model="password" type="password">
					</div>

					<div class="uk-margin">
						<button class="uk-button uk-button-secondary uk-width-1-1" :disabled="loading" type="submit">{{'SUBMIT_LOGIN' | translate}}</button>
					</div>

					<div class="uk-margin uk-text-center">
						<p>
							{{'HAVEACCOUNT' | translate}}
							<router-link :to="{ name: 'users-login'}">
								{{'LOGIN_TITLE' | translate}}
							</router-link>
						</p>
					</div>

				</fieldset>
			</form>
			
		</div>
	</section>
</template>

<script>
	// Import mapMutations in order to call mutations from your store
	import { mapMutations } from 'vuex'
	import strapi from '../../../utils/Strapi'

	export default {
		data() {
			return {
				email: '',
				password: '',
				username: '',
				loading: false
			}
		},
		methods: {
		// Method that will register your users
		async handleSubmit() {
			try {
				this.loading = true
				const response = await strapi.register(
					this.username,
					this.email,
					this.password
					)
				this.loading = false
				// Call your setUser mutation from your auth.js store file
				this.setUser(response.user)
				this.$router.go(-1)
			} catch (err) {
				this.loading = false
				alert(err.message || 'An error occurred.')
			}
		},
		// Define all your needed mutations, here: auth/setUser
		...mapMutations({
			setUser: 'auth/setUser'
		})
	}
}
</script>