import gql from "graphql-tag";

export const HOME_ALL_SERIES = gql`
query allSeries {
	series {
		id
		updatedAt
		Title
		Description
		feature
		is_finish
		cover {
			id
			url
		}
		categories {
			id
			Name
		}
		genres (sort: "name:asc") {
			id
			name
		}
	}
}
`;

export const HOME_ALL_GENRES = gql`
query allGenres {
	genres (sort: "name:asc") {
		id
		name
	}
}
`;

export const NAV_GENRE_ALL_GENRES = HOME_ALL_GENRES;

export const ALL_LINKS_SEARCH_QUERY = gql`
query AllLinksSearchQuery($searchText: String!) {
	allLinks(filter: {
		OR: [{
			url_contains: $searchText
		}, {
			description_contains: $searchText
		}]
	}) {
		id
		url
		description
		createdAt
		postedBy {
			id
			name
		}
		votes {
			id
			user {
				id
			}
		}
	}
}
`;

export const CHAPTER_SINGLE_CHAPTER = gql`
query singleChapter ($id: ID!) {
	chapter(id: $id) {
		id
		name
		chapter_pages {
			id
			page_number
			page_file {
				id
				url
			}
		}
		series {
			id
			Title
			has_chapter
		}
		description
		number
		updatedAt						
		cover {
			id
			url
		}
	}
}
`;

export const AUTHOR_ALL_AUTHORS = gql`
query allAuthors($id: ID!, $userId: ID!) {
	author(id: $id) {
		id
		name
		profession
		bio
		author_instagram
		author_facebook
		author_twitter
		author_web
		avatar {
			id
			url
		}
		series {
			id
			Title
			Description
			categories {
				id
				Name
			}
			genres (sort: "name:asc") {
				id
				name
			}
			cover {
				id
				url
			}
		}
		follow_authors (where: { user: { id_in: [$userId] } }) {
			id
			user {
				id
			}
		}
	}
}
`;

