export const SERIE_SINGLE_SERIE = gql`
query singleSerie($id: ID!, $userId: ID!) {
	serie(id: $id) {
		follow_series(where: { user: { id_in: [$userId] } }) {
			id
			user {
				id
			}
		}
		id
		Title
		is_finish
		has_chapter
		Description
		genres (sort: "name:asc") {
			id
			name
		}
		cover {
			id
			url
		}
		updatedAt
		authors {
			id
			name
			profession
			avatar {
				id
				url
			}
			series {
				id
				Title
				Description
				feature
				is_finish
				cover {
					id
					url
				}
				categories {
					id
					Name
				}
				genres (sort: "name:asc") {
					id
					name
				}
			}
		}
		editorial {
			id
			name
			url
			logo {
				id
				url
			}
		}
		chapters {
			id
			name
			chapter_pages {
				id
				page_number
				page_file {
					id
					url
				}
			}
			series {
				id
				Title
				has_chapter
			}
			description
			number
			updatedAt						
			cover {
				id
				url
			}
		}
	}
}
`;

