<template>
	<div v-if="genre.name" id="it-genre">
		<vue-headful :title="genre.name + ' - Ink&Tales'" />
		<section class="uk-section uk-section-muted uk-section-small">
			<div class="uk-container uk-container-large">
				<h1 class="uk-h2 uk-margin-remove">{{ genre.name }}</h1>

				<ul class="uk-breadcrumb uk-margin-small-top uk-flex-center">
					<li><router-link to="/" >{{'HOME' | translate}}</router-link></li>
					<li><router-link to="/genres" >{{'GENRES' | translate}}</router-link></li>
					<li><span>{{ genre.name }}</span></li>
				</ul>
			</div>
		</section>

		<section class="uk-section">
			<div class="uk-container uk-container-large">
				<SeriesList :series="genre.series || []" :classProp="'uk-child-width-1-5@l'"></SeriesList>
			</div>
		</section>
	</div>
	<Loading v-else/>
</template>

<script>
	import SeriesList from "../components/SeriesList";
	import Loading from "../components/Loading.vue";
	import { GENRE_ALL_GENRES } from "../constants/graphql";

	export default {
		data() {
			return {
				genre: [],
				routeParam: this.$route.params.id
			};
		},
		components: {
			SeriesList,
			Loading
		},
		apollo: {
			genre: {
				query: GENRE_ALL_GENRES,
				variables() {
					return { id: this.routeParam };
				}
			}
		}
	};
</script>