<template>
	<div v-if="serie">
		<div class="uk-flex uk-flex-middle uk-flex-between">
			<div>
				<h3 class="uk-text-large uk-margin-remove">{{serie.chapters.length}} {{'CHAPTERS' | translate}}</h3>
			</div>
			<div class="uk-flex uk-flex-middle">
				<ul class="it-chapter-view uk-subnav uk-margin-remove" uk-switcher="connect: .chapter-sort-class">
					<li>
						<a href="#" class="uk-button uk-button-small"><span uk-icon="icon: list"></span> <span class="uk-visible@s">{{'VIEW_LIST' | translate}}</span></a>
					</li>
					<li>
						<a href="#" class="uk-button uk-button-small"><span uk-icon="icon: thumbnails"></span> <span class="uk-visible@s">{{'VIEW_GRID' | translate}}</span></a>
					</li>
				</ul>

				<button class="it-chapter-sort uk-button uk-button-small" type="button" @click="toggleSort">
					<span v-if="oldToNew">Oldest first <span uk-icon="arrow-up"></span></span>
					<span v-if="!oldToNew">Newest first <span uk-icon="arrow-down"></span></span>
				</button>
			</div>
		</div>

		<ul class="uk-switcher uk-margin chapter-sort-class">
			<li>
				<ul class="uk-list uk-list-divider">
					<li v-for="chapter in serie.chapters" :key="chapter.id">
						<router-link :to="{ path: serie.id + '/chapter/' + chapter.id }" class="uk-link-reset uk-grid-small" uk-grid>
							<div class="uk-width-auto">
								<span class="it-chapter-number uk-article-title uk-text-muted">{{ chapter.number }}</span>
							</div>
							<div class="uk-width-expand">
								<h3 class="uk-margin-remove">
									{{chapter.name}}
								</h3>
								<div class="uk-flex uk-flex-middle">
									<span class="uk-margin-right">{{ moment(serie.updated_at).format("DD MMM YY") }}</span>
									<!-- <span class="uk-margin-right"><span uk-icon="heart"></span> 350</span> -->
									<!-- <span class="uk-margin-right"><span uk-icon="comment"></span> 150</span> -->
									<span class="uk-margin-right">{{chapter.chapter_pages.length}} {{'CHAPTER_PAGES_TOTAL' | translate}}</span>
								</div>
							</div>
						</router-link>
					</li>
				</ul>
			</li>

			<li>
				<div class="uk-child-width-1-2 uk-child-width-1-3@s uk-child-width-1-4@m uk-child-width-1-5@l uk-grid-small uk-grid-match" uk-grid>
					<div v-for="chapter in serie.chapters" :key="chapter.id">
						<router-link :to="{ path: serie.Title + '/chapter/' + chapter.id }" class="uk-link-reset">
							<div class="com-card-chapter uk-card uk-card-small uk-card-default uk-card-hover">
								<div class="uk-card-media-top">
									<img :src="chapter.cover.url" :alt="chapter.name"/>
								</div>
								<div class="uk-card-body">
									<p class="uk-card-badge uk-label">{{'CHAPTER' | translate}} #{{ chapter.number }}</p>
									<h3 class="uk-card-title">{{chapter.name}}</h3>
								</div>
							</div>
						</router-link>
					</div>
				</div>
			</li>
		</ul>
	</div>
</template>

<script>
	var moment = require("moment");
	export default {
		name: "ChapterList",
		data: function() {
			return {
				moment: moment,
				api_url: process.env.VUE_APP_STRAPI_API_URL,
				oldToNew: true,
			};
		},
		methods:{
			toggleSort(){
				this.oldToNew = !this.oldToNew;
				if (this.oldToNew){
					this.serie.chapters = this.serie.chapters.sort(this.oldToNewFuntion);
				} else {
					this.serie.chapters = this.serie.chapters.sort(this.newToOldFuntion);
				}
			},
			oldToNewFuntion(a,b){
				return a.number < b.number ? -1 : 1;
			},
			newToOldFuntion(a,b){
				return a.number < b.number ? 1 : -1;
			}
		},
		props: {
			serie: Object
		},
	};
</script>