<template>
		<section class="uk-section">
			<div class="uk-container uk-container-large">
				<h1 class="uk-article-title">{{ category.Name }}</h1>
				<SeriesList :series="category.series || []"></SeriesList>
			</div>
		</section>
</template>

<script>
	import SeriesList from "../components/SeriesList";
	import { CATEGORY_ALL_CATEGORIES } from "../constants/graphql";

	export default {
		data() {
			return {
				category: [],
				routeParam: this.$route.params.id
			};
		},
		components: {
			SeriesList
		},
		apollo: {
			category: {
				query: CATEGORY_ALL_CATEGORIES,
				variables() {
					return { id: this.routeParam };
				}
			}
		}
	};
</script>