<template>
	<div v-if="chapter" id="com-chapter">
		<vue-headful :title="chapter.series.Title + ', ' + chapter.name + ' - Ink & Tales'" :description="chapter.description" />
		<section class="uk-section">
			<div class="uk-container uk-container-large">
				<div uk-grid>
					<div class="uk-width-1-4@m">
						<figure class="it-cover">
							<img :src="chapter.cover.url" :alt="chapter.name" class="uk-border-rounded" v-if="chapter.cover"/>
							<img src="@/assets/img/placeholder.png" :alt="chapter.name" class="uk-border-rounded" v-else />
						</figure>
					</div>
					<div class="uk-width-3-4@m com-chapter-info">
						<router-link :to="'/serie/' + chapter.series.id" class="uk-button uk-button-primary uk-button-small uk-margin-small-bottom uk-visible@m">
							{{'BACK_TO' | translate}} {{ chapter.series.Title }}
						</router-link>

						<h2 class="uk-margin-remove uk-text-italic uk-hidden@m">{{ chapter.series.Title }}</h2>
						<h1 class="uk-article-title uk-margin-remove">{{ chapter.name }}</h1>
						<p class="uk-article-meta"><span v-if="chapter.updated_at">{{'LASTUPDATE' | translate}} | {{ moment(chapter.updated_at).format("MMM Do YY") }}</span> | <span>{{'CHAPTER_PAGES_TOTAL' | translate}} {{chapter.chapter_pages.length}}</span> </p>
						
						<vue-markdown-it
						v-if="chapter.description"
						:source="chapter.description"
						id="editor"
						/>

						<div>
							<router-link :to="{ path: '/viewer/' + chapter.id + '/' + chapter.chapter_pages[0].id }" class="uk-button uk-button-secondary uk-border-rounded com-button-readnow">
								{{'CHAPTER_READ' | translate}}
							</router-link>

							<div class="uk-child-width-1-3 uk-child-width-1-6@m uk-grid-small uk-grid-match uk-margin-medium-top uk-thumbnav" uk-grid>
								
								<div v-for="chapter_pages in chapter.chapter_pages" :key="chapter_pages.id">
									<router-link :to="{ path: '/viewer/' + chapter.name +  '/' + chapter.id + '/' + chapter_pages.id }">
										<img :src="chapter_pages.page_file.url" class="uk-border-rounded uk-box-shadow-small" />
									</router-link>
								</div>
							</div>
						</div>
					</div>
				</div>
			</div>
		</section>
	</div>
	<Loading v-else/>
</template>

<script>
	var moment = require("moment");
	import VueMarkdownIt from "vue-markdown-it";
	import Loading from "../components/Loading.vue";
	import { CHAPTER_SINGLE_CHAPTER } from "../constants/graphql";

	export default {
		data() {
			return {
				chapter: null,
				moment: moment,
				api_url: process.env.VUE_APP_STRAPI_API_URL,
				chapterId: this.$route.params.chapter
			};
		},
		components: {
			VueMarkdownIt,
			Loading
		},
		apollo: {
			chapter: {
				query: CHAPTER_SINGLE_CHAPTER,
				variables() {
					return {
						id: this.chapterId
					};
				},
				skip(){
					return !this.chapterId
				}
			},			
		}
	};
</script>