<template>
	<div v-if="chapter" id="it-viewer">
		<NavPages :chapter="chapter" :chapter_page="chapter_page" />
		<NavPagesMobile :chapter="chapter" :chapter_page="chapter_page" />
		
		<div class="it-viewer-mobile uk-background-muted uk-text-center uk-hidden@m">
			<figure id="it-viewer-chapter-cover" class="uk-margin-medium-top">
				<caption class="uk-h3 uk-display-block">Portada</caption>
				<img :src="chapter.cover.url" :alt="chapter.name" v-if="chapter.cover" />
				<img src="@/assets/img/placeholder.png" :alt="chapter.name" v-else/>
			</figure>

			<figure :id="'it-page-' + chapter_pages.page_number" v-for="chapter_pages in chapter.chapter_pages" :key="chapter_pages.id" class="uk-margin-medium-top">
				<caption class="uk-h3 uk-display-block uk-text-small">P√°gina {{chapter_pages.page_number}}</caption>
				<img :src="chapter_pages.page_file.url" :alt="chapter.name" v-if="chapter.cover"/>
				<img src="@/assets/img/placeholder.png" :alt="chapter.name" v-else/>
			</figure>

			<figure id="it-viewer-chapter-last" class="uk-margin-medium-top uk-margin-medium-bottom">
				<caption class="uk-h3 uk-display-block uk-text-small">P√°gina</caption>
				<img src="@/assets/img/placeholder.png" :alt="chapter.name"/>
			</figure>
		</div>
		
		<div class="it-viewer-desktop uk-background-muted uk-text-center uk-visible@m">
			<div uk-grid class="uk-grid-collapse">
				<div class="uk-width-3-4 uk-padding uk-background-secondary uk-panel-scrollable">
					<img :src="url" v-bind:class="classes" class="uk-border-rounded" />
				</div>
				<div class="uk-width-1-4 uk-text-left">
					<div class="uk-padding">
						<article>
							<h2>{{chapter.series.Title}}</h2>
							<p>{{chapter.series.Description}}</p>
							<h3>Capitulos</h3>
							<ul v-for="chapter in chapter.series.chapters" :key="chapter.id" class="uk-list uk-list-divider">
								<li>
									<div class="uk-text-small">Number {{chapter.number}}</div>
									<div>{{chapter.name}}</div>
								</li>
							</ul>
						</article>

						<article class="uk-background-primary uk-height-small uk-margin-medium-bottom uk-flex uk-flex-middle uk-flex-center">
							<h3>Publicidad</h3>
						</article>
					</div>
				</div>
			</div>
		</div>

		<Share :serie="serie" />
	</div>
	<Loading v-else/>
</template>

<script>
	import NavPages from "../components/NavPages.vue";
	import Loading from "../components/Loading.vue";
	import Share from "../components/Share.vue";
	import NavPagesMobile from "../components/NavPagesMobile.vue";
	import { mapGetters } from 'vuex';
	import { VIEWER_SINGLE_CHAPTER } from "../constants/graphql";

	export default {
		data() {
			return {
				api_url: process.env.VUE_APP_STRAPI_API_URL,
				chapter: null,
				chapterId: this.$route.params.serie,
				chapterPageId: this.$route.params.chapter,
			};
		},
		computed: {
			...mapGetters('viewer',[
				'currentClass'
				]),
			classes(){
				return this.currentClass;
			},
			chapter_page(){
				return this.chapter.chapter_pages.filter(x => x.id == this.chapterPageId)[0];
			},
			url(){
				const chapter_page = this.chapter.chapter_pages.filter(x => x.id == this.chapterPageId)[0];
				if (chapter_page){
					//return process.env.VUE_APP_STRAPI_API_URL + chapter_page.page_file.url
					return chapter_page.page_file.url
				}else{
					return '';
				}
			},
		},
		mounted () {
			document.body.classList.add('it-viewer')
		},
		destroyed () {
			document.body.classList.remove('it-viewer')
		},
		components: {
			Loading,
			NavPages,
			Share,
			NavPagesMobile
		},
		apollo: {
			chapter: {
				query: VIEWER_SINGLE_CHAPTER,
				variables() {
					return {
						id: this.chapterId
					};
				},
				skip () {
					console.log('this.chapterId', this.chapterId);
					return !this.chapterId
				},
			},			
		}
	}
</script>