<template>
	<div v-if="chapter.id" uk-sticky="sel-target: .uk-navbar-container; cls-active: uk-navbar-sticky">
		<nav id="it-nav-viewer" class="uk-navbar-container uk-light uk-visible@s" uk-navbar>
			<div class="uk-navbar-left">
				<router-link :to="back" class="uk-navbar-item uk-logo">
					Back
				</router-link>
			</div>
			<div class="uk-navbar-center">
				<ul class="uk-navbar-nav" v-if="chapter">
					<li v-if="!previous"><a href="#" class="uk-disabled"><span uk-icon="chevron-left"></span> Previous</a></li>
					<li v-else><router-link :to="previous"><span uk-icon="chevron-left"></span> Previous</router-link></li>

					<li v-if="!next"><a href="#" class="uk-disabled">Next <span uk-icon="chevron-right"></span></a></li>
					<li v-else><router-link :to="next">Next <span uk-icon="chevron-right"></span></router-link></li>

					<li><a href="#" @click="viewFit('fit-height')">Fit ‚Üï</a></li>
					<li><a href="#" @click="viewFit('fit-width')">Fit ‚Üî</a></li>
					
					<li class="uk-visible@l" v-if="!isFullscreen">
						<a href="#" @click="go_full_screen">Fullscreen <span uk-icon="expand"></span></a>
					</li>
					<li class="uk-visible@l" v-if="isFullscreen">
						<a href="#" @click="close_full_screen">Exit Fullscreen <span uk-icon="expand"></span></a>
					</li>
				</ul>
			</div>

			<div class="uk-navbar-right">
				<ul class="uk-navbar-nav">
					<li>
						<a href="#">Go to page {{ chapter_page.page_number }}</a>
						<div class="uk-navbar-dropdown">
							<ul class="uk-nav uk-navbar-dropdown-nav">
								<li v-for="chapter_page in chapter.chapter_pages" :key="chapter_page.id" :value="chapter_page.page_number">
									<router-link :to="'/viewer/' + chapter.id + '/' + chapter_page.id">
										Page {{chapter_page.page_number}}
									</router-link>
								</li>
							</ul>
						</div>
					</li>
				</ul>
			</div>
		</nav>
	</div>
</template>

<script>
	import { mapMutations } from 'vuex';
	export default {
		name: "NavPages",
		props: {
			chapter_page: Object,
			chapter: Object
		},
		data() {
			return {
				isActive: false,
				isFullscreen: false,
			}
		},
		computed:{
			back() {
				if (this.chapter.series.has_chapter) {
					return '/serie/'+ this.chapter.series.id +'/chapter/' + this.chapter.id
				}
				else {
					return '/serie/'+ this.chapter.series.id
				}
			},
			next(){
				let index = this.currentIndex();
				if (this.maxPages() > index + 1 ) {
					return this.urlFor(index + 1);
				}
				return null;
			},
			previous(){
				let index = this.currentIndex();
				if (index > 0) {
					return this.urlFor(index - 1);
				}
				return null;
			}
		},
		methods:{
			...mapMutations('viewer',[
				'setFitClass'
				]),
			maxPages(){
				return this.chapter.chapter_pages.length;
			},
			currentIndex(){
				return this.chapter.chapter_pages.indexOf(this.chapter_page);
			},
			urlFor(index) {
				return { path: '/viewer/' + this.chapter.id + '/' + this.chapter.chapter_pages[index]?.id };
			},
			viewFit (value) {
				this.setFitClass(value);
			},
			go_full_screen(){
				this.isFullscreen = true;
				var elem = document.documentElement;
				if (elem.requestFullscreen) {
					elem.requestFullscreen();
				} else if (elem.msRequestFullscreen) {
					elem.msRequestFullscreen();
				} else if (elem.mozRequestFullScreen) {
					elem.mozRequestFullScreen();
				} else if (elem.webkitRequestFullscreen) {
					elem.webkitRequestFullscreen();
				}
			},
			close_full_screen(){
				this.isFullscreen = false;
				if (document.exitFullscreen) {
					document.exitFullscreen();
				} else if (document.mozCancelFullScreen) {
					document.mozCancelFullScreen();
				} else if (document.webkitCancelFullScreen) {
					document.webkitCancelFullScreen();
				}
			}
		}
	};
</script>
