import './assets/css/style.scss'
import Vue from "vue"
import VueApollo from "vue-apollo"
import VueRouter from "vue-router"
import apolloClient from "./vue-apollo"
import VueSocialSharing from 'vue-social-sharing'
import App from "./App.vue"
import store from './../store/index'
import VueMeta from 'vue-meta'

/* lang filters */
import './filters/translate.filter';

/* atributes on head like title */
import vueHeadful from 'vue-headful';

Vue.use(VueApollo);
Vue.use(VueRouter);
Vue.config.productionTip = false;
Vue.use(VueSocialSharing);
Vue.component('vue-headful', vueHeadful);
Vue.use(VueMeta);

const apolloProvider = new VueApollo({
	defaultClient: apolloClient
});

const router = new VueRouter({
	mode: "hash",
	routes: [
	{
		path: "/",
		name: 'home',
		components: require("./containers/Home.vue")
	},
	{
		path: "/about",
		name: 'about',
		components: require("./containers/About.vue")
	},
	{
		path: "/help",
		name: 'help',
		components: require("./containers/Help.vue")
	},
	{
		path: "/publish",
		name: 'publish',
		components: require("./containers/Publish.vue")
	},
	{
		path: "/shop",
		name: 'shop',
		components: require("./containers/Shop.vue")
	},
	{
		path: "/series/",
		name: 'series',
		components: require("./containers/Series.vue")
	},
	{
		path: "/serie/:id",
		name: 'serie',
		components: require("./containers/Serie.vue")
	},
	{
		path: "/serie/:serie/chapter/:chapter",
		components: require("./containers/Chapter.vue")
	},
	{
		path: "/category/:id",
		components: require("./containers/Category.vue")
	},
	{
		path: "/genres/",
		name: 'genres',
		components: require("./containers/Genres.vue")
	},
	{
		path: "/genre/:id",
		components: require("./containers/Genre.vue")
	},
	{
		path: "/author/:id",
		components: require("./containers/Author.vue")
	},
	{
		path: "/authors/",
		name: 'authors',
		components: require("./containers/Authors.vue")
	},
	{
		path: "/viewer/:serie/:chapter",
		name: 'viewer',
		components: require("./containers/Viewer.vue")
	},
	{
		path: "/users/register",
		name: 'users-register',
		components: require("./containers/users/Register.vue")
	},
	{
		path: "/users/login",
		name: 'users-login',
		components: require("./containers/users/Login.vue")
	},
	{
		path: "/users/profile",
		name: 'profile',
		components: require("./containers/users/Profile.vue")
	},
	{
		path: '/search',
		name: 'search',
		components: require("./containers/Search.vue")
	},
	{
		path: "*",
		name: 'not-found',
		component: require("./containers/NotFound.vue")
	}
	],
	// eslint-disable-next-line no-unused-vars
	scrollBehavior (to, from, savedPosition) {
		return { x: 0, y: 0 }; 
	},
});

new Vue({
	apolloProvider,
	router,
	store,
	render: h => h(App)
}).$mount("#app");
