<template>
	<section v-if="user" id="it-user-profile" class="uk-section">
		<div class="uk-container uk-container-large">
			<div uk-grid>
				<aside class="uk-width-1-3@m uk-text-center">
					<article class="uk-card uk-card-body uk-card-default ik-author-card">
						<figure class="ik-author-avatar">
							<img src="@/assets/img/genre.png" class="uk-border-circle" />
						</figure>

						<h1 class="uk-h2 uk-margin-remove">{{ user.username }}</h1>

						<dl class="uk-description-list uk-description-list-divider uk-text-left">
							<dt>ID</dt>
							<dd>{{ user.id }}</dd>
							<dt>{{'USERNAME' | translate}}</dt>
							<dd>{{ user.username }}</dd>
							<dt>{{'EMAIL' | translate}}</dt>
							<dd>{{ user.email }}</dd><!-- <dt>Language</dt><dd>{{ selectedLanguage }}</dd> -->
						</dl>
					</article>
				</aside>

				<div class="com-author-library uk-width-2-3@m">
					<!-- My favorites -->
					<section>
						<h3 class="uk-text-large uk-article-title">{{'MY_FAVORITES' | translate}}</h3>
						<SeriesList
						v-if="user.follow_series.length > 0"
						:series="user.follow_series.map(x => x.series) || []" :classProp="'uk-child-width-1-3@l'"
						></SeriesList>
						<p v-else uk-alert>{{'NO_FAVORITES' | translate}}</p>
					</section>

					<!-- My follow authors -->
					<section class="uk-margin-large-top">
						<h3 class="uk-text-large uk-article-title">Mis autores</h3>
						<AuthorList v-if="user.follow_authors.length > 0" :authors="user.follow_authors.map(x => x.author) || []"></AuthorList>
						<p v-else uk-alert>{{'AUTHOR_NOFOLLOW' | translate}}</p>
					</section>
				</div>
			</div>
		</div>
	</section>
	<Loading v-else />
</template>

<script>
	import SeriesList from "../../components/SeriesList.vue";
	import AuthorList from "../../components/AuthorList.vue";
	import Loading from "../../components/Loading.vue";
	import { USERS_PROFILE_SINGLE_USER } from "../../constants/graphql";
	import { mapGetters } from "vuex";

	export default {
		mounted: function() {
			setTimeout(() => {
				this.id = this.$store.getters["auth/getId"];
			}, 9000);
		},
		data() {
			return {
				api_url: process.env.VUE_APP_STRAPI_API_URL,
				user: null,
				routeParam: this.$route.params.id,
				id: null
			};
		},
		components: {
			SeriesList,
			AuthorList,
			Loading
		},
		computed: {
			...mapGetters("i18n", ["currentLanguage"]),
			selectedLanguage: {
				get() {
					return this.currentLanguage;
				}
			},
		},
		apollo: {
			user: {
				query: USERS_PROFILE_SINGLE_USER,
				fetchPolicy: 'no-cache',
				variables() {
					return { id: this.id };
				},
				skip() {
					return !this.id;
				}
			}
		}
	};
</script>