import Cookies from 'js-cookie'

export default {
	namespaced: true,

	state: () => { },

	mutations: {
		setUser(state, user) {
			state.user = user;
		},
	},

	getters: {
		getUsername: state => {
			if (state.user)
				return state.user.username;
			else return null;
		},
		getId: state => {
			if (state.user)
				return state.user.id;
			else return null;
		}
	},

	actions: {
		findUserFromCookie(context) {
			let user = Cookies.get('user');
			if (!user) {
				return
			}
			setTimeout(() => {
				context.commit('setUser', JSON.parse(user));
			}, 1);

		},
		saveAuthInCookie(context, user) {
			context.commit('setUser', user);
			Cookies.set('user', user);
		},
		logout(context) {
			context.dispatch("saveAuthInCookie", null)
		}
	}
}
