import languages from '../src/assets/localization/languages';
import Cookies from 'js-cookie';

export default {
	namespaced: true,

	state: {
		currentLanguage: languages[0].id
	},

	mutations: {
		setCurrentLanguage(state, language) {
			state.currentLanguage = language;
			Cookies.set('currentLanguage', state.currentLanguage);
		}
	},

	actions:{
		findLanguageFromCookie(context) {
			let language = Cookies.get('currentLanguage');
			if (!language) {
				return
			}
			setTimeout(() => {
				context.commit('setCurrentLanguage', language);
			}, 1);

		},
	},
	
	getters: {
		currentLanguage: state => state.currentLanguage,
	}
}
