<template>
	<div v-if="series && series.length > 0" id="it-search-results">	
		<section class="uk-section uk-section-muted uk-section-small">
			<div class="uk-container uk-container-large">
				<h1 class="uk-h2 uk-margin-remove">{{'SEARCH_RESULT' | translate}} "{{ searchedWord }}"</h1>
			</div>
		</section>

		<section class="uk-section">
			<div class="uk-container uk-container-large">
				<SeriesList v-if="series && series.length > 0" :series="series"></SeriesList>
				<h4 v-else>No se encontraron resultados</h4>
			</div>
		</section>
	</div>
	<Loading v-else/>
</template>

<script>
	import SeriesList from "../components/SeriesList.vue";
	import Loading from "../components/Loading.vue";
	import { SEARCH_SEARCH_SERIES } from "../constants/graphql";

	export default {
		components: {
			SeriesList,
			Loading
		},
		data() {
			return {
				series: []
			};
		},
		computed: {
			searchedWord(){
				return this.$route.query.q;
			}
		},
		apollo: {
			series: {
				query: SEARCH_SEARCH_SERIES,
				variables() {
					return {
						searchText: this.$route.query.q
					};
				},
			},
		}
	};
</script>

