<template>
	<div id="app">
		<div id="it-grid" class="uk-grid uk-grid-match uk-grid-collapse">
			<Nav v-if="shownav" />
			<div id="it-content" class="uk-width-4-5@m uk-width-5-6@xl">
				<transition name="fade" mode="out-in">
					<main class="it-main">
						<router-view :key="$route.fullPath"></router-view>
					</main>
				</transition>
			</div>
		</div>
	</div>
</template>

<script>
	import UIkit from 'uikit';
	import Icons from 'uikit/dist/js/uikit-icons';
	UIkit.use(Icons);

	import Nav from "./components/Nav.vue";

	export default {
		name: "App",
		mounted: function() {
			this.$store.dispatch("auth/findUserFromCookie")
			this.$store.dispatch("i18n/findLanguageFromCookie")
		},
		computed:{
			shownav(){
				return this.$route.name != 'viewer'
			},
			showfooter(){
				return this.$route.name != 'viewer'
			},
		},
		components: {
			Nav
		}
	};
</script>

