<template>
	<div id="modal-login" class="uk-flex-top" uk-modal>
		<div class="uk-modal-dialog uk-margin-auto-vertical">
			<button class="uk-modal-close-default" type="button" uk-close></button>
			<div class="uk-modal-header uk-text-center">
				<h2 class="uk-modal-title">{{'LOGIN_TITLE' | translate}}</h2>
			</div>
			<div class="uk-modal-body">
				<form @submit.stop.prevent="handleSubmit">
					<fieldset class="uk-fieldset">
						<div class="uk-margin">
							<label class="uk-form-label" for="form-stacked-text">{{'EMAIL' | translate}}</label>
							<input class="uk-input" v-model="email" type="email" placeholder="pedro@gmail.com">
						</div>

						<div class="uk-margin">
							<label class="uk-form-label" for="form-stacked-text">{{'PASSWORD' | translate}}</label>
							<input class="uk-input" v-model="password" type="password">
						</div>

						<div class="uk-margin">
							<button class="uk-button uk-button-secondary uk-width-1-1" :disabled="loading" type="submit">{{'SUBMIT_LOGIN' | translate}}</button>
						</div>

						<div class="uk-margin uk-text-center">
							<p>
								{{'NOACCOUNT' | translate}}
								<router-link :to="{ name: 'users-register'}" class="uk-modal-close">
									{{'REGISTER' | translate}}
								</router-link>
							</p>
						</div>
					</fieldset>
				</form>
			</div>
		</div>
	</div>
</template>

<script>
	import strapi from '../../../utils/Strapi'
	import { mapGetters, mapMutations } from "vuex";

	export default {
		name: "Login",
		data() {
			return {
				email: '',
				password: '',
				loading: false
			}
		},
		computed:{
			...mapGetters("i18n", ["currentLanguage"]),
			selectedLanguage: {
				get() {
					return this.currentLanguage;
				}
			},
		},
		methods: {
			...mapMutations("i18n", ["setCurrentLanguage"]),
			async handleSubmit() {
				try {
					this.loading = true
					const response = await strapi.login(
						this.email,
						this.password
						)
					this.loading = false
					this.$store.dispatch("auth/saveAuthInCookie", response.user);
					this.$store.dispatch("i18n/setCurrentLanguage", response.user.language);
					this.setCurrentLanguage(response.user.language);
					this.$router.go()
				} catch (err) {
					this.loading = false
					alert(err.message || 'An error occurred.')
				}
			},
		}
	}
</script>