<template>
	<div v-if="serie" id="it-serie">
		<vue-headful :title="serie.Title + ' - Ink & Tales'" :description="serie.Description" />
		
		<section id="it-serie-hero" class="uk-section uk-section-small uk-text-center uk-text-left@m">
			<div class="uk-container uk-container-large">
				<div class="uk-flex-middle" uk-grid>
					<div class="uk-width-2-5@m uk-width-1-2@l it-serie-art uk-hidden@m">
						<figure class="it-cover">
							<img :src="serie.cover.url" class="uk-border-rounded" :alt="serie.Title" v-if="serie.cover"/>
							<img src="@/assets/img/placeholder.png" class="uk-border-rounded" :alt="serie.Title" v-else />
						</figure>
					</div>
					<div class="uk-width-3-5@m uk-width-1-2@l it-serie-info">
						<!-- Serie Genres -->
						<p v-if="serie.genres" class="it-serie-genre">
							<span v-for="genre in serie.genres" :key="genre.id">
								{{ genre.name }}
							</span>
						</p>

						<!-- Serie Title -->
						<h1 class="it-serie-title uk-margin-remove">{{ serie.Title }}</h1>

						<!-- Serie Author -->
						<h2 class="it-serie-author uk-text-large uk-margin-small-top">
							<span v-for="author in serie.authors" :key="author.id">
								<router-link :to="{ path: '/author/' + author.id }" :key="author.id" class="uk-link-reset" >
									{{ author.name }}
								</router-link>
							</span>
						</h2>

						<p class="it-serie-meta uk-article-meta uk-flex uk-flex-middle uk-flex-center uk-flex-left@m">
							<span class="uk-label uk-label-primary uk-margin-small-right">
								<span v-if="serie.is_finish">{{'COMPLETE' | translate}}</span>
								<span v-else>{{'ONGOING' | translate}}</span>
							</span>
							<a :href="serie.editorial.url" target="_blank" v-if="serie.editorial" class="uk-margin-small-right">
								{{'EDITORIAL' | translate}} {{ serie.editorial.name }}
							</a>
							<span v-if="serie.updated_at" class="uk-visible@m">
								{{'LASTUPDATE' | translate}} {{ moment(serie.updated_at).format("DD MMM YY") }}
							</span>
						</p>

						<vue-markdown-it v-if="serie.Description" :source="serie.Description" class="it-serie-description"></vue-markdown-it>

						<nav class="it-serie-actionbar uk-flex uk-flex-middle uk-visible@m">
							<router-link :to="read" class="it-button-readnow uk-button uk-button-large uk-button-dark" v-if="read">
								{{'SERIE_READ' | translate}}
							</router-link>

							<button v-if="serie.follow_series.length == 0" class="ik-button-fav uk-button uk-button-large uk-button-default addtofav" type="button" @click="addToFav">
								<span uk-icon="heart"></span>
							</button>

							<button v-else class="ik-button-fav uk-button uk-button-large uk-button-default removefromfav" type="button" @click="removeFromFav">
								<span uk-icon="check"></span>
							</button>
							
							<button class="uk-button uk-button-large uk-button-default" type="button" uk-toggle="target: #modal-share"><span uk-icon="social"></span></button>

							<Share :serie="serie" />
						</nav>
					</div>
					<div id="it-serie-cover" class="uk-width-2-5@m uk-width-1-2@l uk-visible@m">
						<figure class="it-cover">
							<img :src="serie.cover.url" class="uk-border-rounded" :alt="serie.Title" v-if="serie.cover"/>
							<img src="@/assets/img/placeholder.png" class="uk-border-rounded" :alt="serie.Title" v-else />
						</figure>
					</div>
				</div>
			</div>
		</section>

		<!-- If the serie has many chapters -->
		<section class="uk-section uk-section-small" v-if="serie.has_chapter">
			<div class="uk-container uk-container-large">
				<ChapterList :serie="serie"></ChapterList>
			</div>
		</section>

		<!-- If the serie has only one chapter -->
		<!--<section class="uk-section" v-else>
			<div class="uk-container uk-container-large">
				<div class="uk-child-width-1-6@m uk-grid-small uk-grid-match uk-margin-medium-top" uk-grid>
					<div v-for="chapter_pages in serie.chapters[0].chapter_pages" :key="chapter_pages.id">
						<router-link :to="{ path: '/viewer/' + serie.chapters[0].name +  '/' + serie.chapters[0].id + '/' + chapter_pages.id }">
							<img :src="api_url + chapter_pages.page_file.url" class="uk-border-rounded uk-box-shadow-small" />
						</router-link>
					</div>
				</div>
			</div>
		</section>-->

		<!-- Related -->
		<section id="it-serie-related" class="uk-section">
			<div class="uk-container uk-container-large">
				<h3 class="uk-text-large uk-article-title">{{'RELATED' | translate}}</h3>
				<SeriesRelated :serie="serie"></SeriesRelated>
			</div>
		</section>

		<!-- Action nav mobile -->
		<nav id="it-serie-nav" class="uk-navbar-container uk-position-fixed uk-position-bottom uk-hidden@m" uk-navbar>
			<div class="uk-navbar-left">
				<ul class="uk-navbar-nav">
					<li>
						<router-link :to="read" class="it-button-readnow" v-if="read">
							{{'SERIE_READ' | translate}}
						</router-link>
						<a href="#" class="it-button-readnow uk-disabled" v-else>
							{{'SERIE_READ' | translate}}
						</a>
					</li>
				</ul>
			</div>
			<div class="uk-navbar-right">
				<ul class="uk-navbar-nav">
					<li>
						<a v-if="serie.follow_series.length == 0" class="ik-button-fav addtofav" @click="addToFav">
							<span uk-icon="heart"></span>
						</a>
						<a v-else class="ik-button-fav removefromfav" @click="removeFromFav">
							<span uk-icon="check"></span>
						</a>
					</li>
					<li><a href="#modal-share" uk-toggle><span uk-icon="social"></span></a><Share :serie="serie" /></li>
				</ul>
			</div>
		</nav>
	</div>
	<Loading v-else/>
</template>

<script>
	var moment = require("moment");

	import VueMarkdownIt from "vue-markdown-it";
	import { SERIE_SINGLE_SERIE } from "../constants/graphql";
	import strapi from "../../utils/Strapi";

	import ChapterList from "../components/ChapterList.vue";
	import SeriesRelated from "../components/SeriesRelated.vue";
	import Share from "../components/Share.vue";
	import Loading from "../components/Loading.vue";

	export default {
		mounted: function() {
			setTimeout(() => {
				this.id = this.$store.getters["auth/getId"];
				this.$apollo.queries.serie.refetch();
			}, 100);
		},
		data() {
			return {
				serie: null,
				moment: moment,
				api_url: process.env.VUE_APP_STRAPI_API_URL,
				routeParam: this.$route.params.id,
				id: null
			};
		},
		computed: {
			read() {
				if (
					!this.serie ||
					!this.serie.chapters ||
					this.serie.chapters.length == 0
					)
					return null;
				var chapter = this.serie.chapters[0];
				return {
					path:
					"/viewer/" +
					chapter.id +
					"/" +
					chapter.chapter_pages[0]?.id
				};
			}
		},
		methods: {
			async addToFav() {
				await strapi
				.createEntry("follow-series", {
					series: {
						id: this.serie.id
					},
					user: {
						id: this.id
					}
				})
				.then(follow_serie => {
					this.serie.follow_series.push(follow_serie);
				})
				.catch(err => console.debug("err", err));
			},
			removeFromFav() {
				this.serie.follow_series.forEach(follow_serie => {
					strapi
					.deleteEntry("follow-series", follow_serie.id)
					.then(() => {
						this.serie.follow_series = [];
					})
					.catch(err => console.debug("err", err));
				});
			}
		},
		components: {
			Loading,
			VueMarkdownIt,
			ChapterList,
			SeriesRelated,
			Share
		},
		apollo: {
			serie: {
				query: SERIE_SINGLE_SERIE,
				variables() {
					return {
						id: this.routeParam,
						userId: this.id ?? 0
					};
				},
			}
		}
	};
</script>